.sidebar {
  flex: 0.35;
  display: flex;
  flex-direction: column;
}
.sidebar1 {
  flex: 0.35;
  display: flex;
  flex-direction: column;
}

.sidebar_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  padding-right: 5px;
  border-right: 1px solid lightgray;
}

.size1 {
  width: 35px !important;
  height: 35px !important;
}

.size {
  width: 20px !important;
  height: 20px !important;
}

.sidebar_header_right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 10vw;
  margin-left: auto;
}

.sidebar_searchcointainer > .MuiSvgIcon-root {
  color: gray;
}

.sidebar_search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 39px;
  padding: 10px;
  border-right: 1px solid lightgray;
}

.sidebar_searchcointainer {
  background-color: white;
  display: flex;
  align-items: center;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}

.sidebar_searchcointainer > input {
  border: none;
  margin-left: 10px;
}

.sidebar_searchcointainer > input:focus {
  outline: none;
}

.sidebar_chats {
  display: flex;
  flex-direction: column;
  border-right: 1px solid lightgray;
  background-color: white;
  overflow-y: scroll;
  flex: 1;
}

.sidebar_chats::-webkit-scrollbar {
  display: block;
}
@media only screen and (max-width: 600px) {
  .sidebar1 {
    flex: 1;
    display: block;
  }
  .sidebar {
    display: none;
  }
}
