.sidechat {
  flex: 0.65;
  display: flex;
  flex-direction: column;
}
.link {
  color: black !important;
  text-decoration: none !important;
}
.sidechat_header {
  padding: 20px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid lightgray;
}

.sidechat_body {
  flex: 1;

  background-image: url(https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png);
  background-repeat: repeat;
  background-position: center;
  padding: 30px;
  overflow: scroll;
}

.sidechat_name {
  position: absolute;
  top: -15px;
  font-size: x-small;
  font-weight: 700;
}

.sidechat_time {
  margin-left: 10px;
  font-size: xx-small;
}
.remove {
  background-color: white;
  border-radius: 12px;
  box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.8);
  position: absolute;
  max-height: 100px;
  overflow: hidden;
  transition: 0.75s all ease;
  /* height: 0px; */
  top: 40px;
  /* padding: 10px; */
}
.remove:before {
  content: "";
  /* position: absolute; */
  /* transform: rotateZ(45deg); */
  background-color: black;
  height: 100px;
  top: 100px;
}
.remove > p {
  margin: 10px;
}
.remove:hover {
  background-color: rgb(199, 195, 195);
  cursor: pointer;
}

.chat_message {
  position: relative;
  max-width: 90%;
  font-size: 14px;
  background-color: white;
  padding: 6px;
  /* padding-top: 12px; */
  margin: 0px 0px 25px 0px;
  border-radius: 10px;
  width: fit-content;
}

.sidechat_header_info {
  flex: 1;
  padding-left: 20px;
}

.sidechat_header_info > h3 {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 0px;
}

.sidechat_header_info > p {
  color: gray;
  font-size: 12px;
}

.sidechat_header_right {
  position: relative;
  display: flex;
  justify-content: space-between;
  min-width: 100px;
}

.sidechat_footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.sidechat_footer button {
  display: none;
}

.sidechat_footer > form {
  flex: 1;
  display: flex;
}

.sidechat_footer input {
  padding: 7px;

  /* height: 15px !important; */
  resize: none;
  word-wrap: break-word;
  word-break: break-all;
  /* color: lightgray; */
  border-radius: 24px;
  flex: 1;
  border: 1px solid lightgray;
  overflow-wrap: break-word;
}
input {
  vertical-align: top !important;
}
.sidechat_footer textarea:focus {
  outline: none;
}

.message {
  color: gray !important;
  color: gray;
}

.sidechat_body::-webkit-scrollbar {
  display: none;
}

.chat_receiver {
  background-color: #dcf8c6;
  margin-left: auto;
}

@media only screen and (max-width: 600px) {
  .sidechat {
    flex: 1;
  }
}
